import React from 'react'

function skills() {
    return (
           <section id="skills" className="skills-section">
        <div className="container ">
          <header className="text-center">
            <h2 data-animate="fadeInDown" className="title">Skills</h2>
          </header>
          <div className="row">
                <div data-animate="fadeInUp" className="col-lg-6 mx-auto ">
                    <h5 className="skills-title">Key Skills</h5>
                    <ul>
                        <li>Javascript</li>
                        <li>HTML5</li>
                        <li>CSS3</li>
                        <li>Bootstrap</li>
                        <li>React JS</li>
                        <li>NPM</li>
                        <li>Github</li>
                        <li>MS OFFICE</li>
                        <li>Agile Methodologies</li>
                    </ul>
                </div>
                <div data-animate="fadeInUp" className="col-lg-6 mx-auto">
                    <h5 className="skills-title">Transferable Skills</h5>
                    <ul>
                        <li>Teamwork</li>
                        <li>Adaptability</li>
                        <li>Prioritising</li>
                        <li>Problem Solving</li>
                        <li>Time Management</li>
                        <li>Fast Learner</li>
                        <li>Customer Service Skills</li>

                    </ul>
                </div>
            </div>
        </div>
      </section> 
    )
}

export default skills
